import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';


const useStyles = makeStyles((theme) => ({
  mainFeaturedPost: {
    position: 'relative',
    backgroundColor: theme.palette.grey[800],
    color: theme.palette.common.white,
    marginBottom: theme.spacing(4),
    backgroundImage: 'url(https://source.unsplash.com/random)',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
  },
  overlay: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    right: 0,
    left: 0,
    backgroundColor: 'rgba(0,0,0,.3)',
  },
  mainFeaturedPostContent: {
    position: 'relative',
    padding: theme.spacing(3),
    [theme.breakpoints.up('md')]: {
      padding: theme.spacing(6),
      paddingRight: 0,
    },
  },
  title: {
    textAlign: 'center',
    color: '#A5D028',
  },
  socialLink: {
    color: 'white',
  },
  description: {
    color: '#BCEF2B',
    textAlign: 'center',
    fontStyle: 'italic',
  },
}));

export default function MainPageContent(props) {
  const classes = useStyles();
  const { post } = props;

  return (
    <Paper className={classes.mainFeaturedPost} style={{ backgroundImage: `url(${post.image})` }}>
      {/* Increase the priority of the hero background image */}
      {<img style={{ display: 'none' }} src={post.image} alt={post.imageText} />}
      <div className={classes.overlay} />
      <Grid container>
        <Grid item md={11}>
          <div className={classes.mainFeaturedPostContent}>
            <Typography className={classes.title} component="h1" variant="h3" color="inherit" gutterBottom>
              {post.title}
            </Typography>
            <Typography className={classes.description} variant="h5" color="inherit" paragraph>
              {post.description}
            </Typography>
            <Grid item md={2}>
            <Link className={classes.socialLink} display="block" variant="body1" href="https://github.com/ansakhar">
            <Grid container direction="row" spacing={1} alignItems="center">
            <Grid item><post.icon1 /></Grid>
            <Grid item>{post.social1}</Grid>
            </Grid>
            </Link>
            <Link className={classes.socialLink} display="block" variant="body1" href="https://www.linkedin.com/in/anna-sakharovskaya">
            <Grid container direction="row" spacing={1} alignItems="center">
            <Grid item><post.icon2 /></Grid>
            <Grid item>{post.social2}</Grid>
            </Grid>
            </Link>
            </Grid>
          </div>
        </Grid>
      </Grid>
    </Paper>
  );
}

MainPageContent.propTypes = {
  post: PropTypes.object,
};