import React from 'react';
import Header from './Header';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import footerData from './footerData';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Box from '@material-ui/core/Box';

const useStyles = makeStyles((theme) => ({
  contactInformation: {
   // marginTop: theme.spacing(2),
    marginBottom: theme.spacing(5),
  },
  link: {
    marginRight: theme.spacing(5),
    marginTop: theme.spacing(5),
  },
}));

export default function Contact() {
  const classes = useStyles();
    return (
          <Container maxWidth="lg">
          <Header title="" />
          <Box mt={4}>
          <Typography className={classes.contactInformation} variant="h5" align="center" gutterBottom>
        Contact Information
        </Typography>
        <Typography variant="subtitle1" align="center" gutterBottom>
        <footerData.phoneIcon /> {footerData.phone}
        </Typography>
        <Typography variant="subtitle1" align="center" gutterBottom>
        <footerData.emailIcon /> {footerData.email}
        </Typography>
        <Typography variant="subtitle1" align="center" gutterBottom>
        <footerData.locationIcon /> {footerData.location}
        </Typography>
        <Grid container direction="row"  justifyContent="center">
        <Link color='inherit' href="https://github.com/ansakhar">
        <Grid><footerData.iconGitHub fontSize="large"  className={classes.link}/></Grid>
        </Link>
        <Link color='inherit' href="https://www.linkedin.com/in/anna-sakharovskaya">
        <Grid><footerData.iconLinkedin fontSize="large" className={classes.link}/></Grid>
        </Link>
        </Grid>
       </Box>
      </Container>
    );
  }