import React from 'react';
import Header from './Header';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Hidden from '@material-ui/core/Hidden';
import Link from '@material-ui/core/Link';
import GitHubIcon from '@material-ui/icons/GitHub';
import Box from '@material-ui/core/Box';
import courses from './courses';
import { ThemeProvider, createTheme} from '@material-ui/core/styles';

const theme = createTheme();
theme.typography.h3 = {
  fontSize: 24,
  fontWeight: 'bold'
};
theme.typography.subtitle1 = {
  fontSize: 18,
  fontWeight: 'regular',
  lineHeight: 1.5
};
theme.typography.subtitle2 = {
  fontSize: 18,
  fontWeight: 'regular',
  lineHeight: 2
};

const useStyles = makeStyles({
  card: {
    display: 'flex',
    height: 320,
    overflow: 'auto'
  },
  cardDetails: {
    flex: 1,
    borderColor: 'primary.main',
  },
  cardMedia: {
    width: 180
  },
  githublLink: {
    fontSize: 16,
  fontWeight: 'regular'
  },
  link: {
    fontSize: 16,
  fontWeight: 'regular'
  }
});

function LinkExample(props) {
  const classes = useStyles();
  const { links } = props;
  if (links !== undefined) 
  return (
   
    <Grid >
              {links.map((link) => (
        <Link className={classes.link} display="block" variant="body1" href={link.url} key={link.title}>
          {link.title}
        </Link>
      ))}
      </Grid>
  ); if (links === undefined)  return (
<Grid ></Grid>
  )
}

function  Course(props) {
  const classes = useStyles();
  const { course, links } = props;

  return (
    <Grid item xs={12} md={6}>
      <Box boxShadow={3} >
        <Card className={classes.card}>
          <div className={classes.cardDetails}>
            <CardContent>
            <ThemeProvider theme={theme}>
              <Typography variant="h3">
                {course.title}
              </Typography>
              
              <Typography variant="subtitle2" color="textSecondary">
                {course.date}
              </Typography>
              <Typography variant="subtitle1" paragraph>
                {course.description}
              </Typography>
              </ThemeProvider>
              <Grid >
              <Link className={classes.githublLink} display="block" variant="body1" href={course.github}>
            <Grid container direction="row" spacing={1} alignItems="center">
            <Grid item><GitHubIcon /></Grid><Grid item>GitHub</Grid>
            </Grid>
            </Link>
            </Grid>
              <LinkExample links={links}/>
            </CardContent>
          </div>
          <Hidden xsDown>
            <CardMedia className={classes.cardMedia} image={course.image} title={course.imageTitle} />
          </Hidden>
        </Card>
        </Box>
    </Grid>
  );
}

export default function  Portfolio() {

  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg">
        <Header title="" />
        <Box mt={4}>
<Grid container spacing={4}>
            {courses.map((course) => (
              <Course key={course.title} course={course} links={course.links} />
            ))}
          </Grid>
          </Box>
          </Container>
    </React.Fragment>
  )
            }

Portfolio.propTypes = {
  course: PropTypes.object,
};